/*progressbar*/
#progressbar {
	width: 100%;
	margin-left: 10em;
	margin-bottom: 2em;
	overflow: hidden;
	/*CSS counters to number the steps*/
	counter-reset: step;
}
#progressbar li {
	list-style-type: none;
	text-transform: uppercase;
	font-size: 16px;
	width: 33%;
	float: left;
	position: relative;
}
#progressbar li:before {
	content: counter(step);
	counter-increment: step;
	width: 3em;
	line-height: 3em;
	display: block;
	font-size: 14px;
	text-align: center;
	color: #B0B4BD;
	background: #E1E4E9;
	border-radius: 50%;
	margin: 0 auto 5px 0px;
}
/*progressbar connectors*/
#progressbar li:after {
	content: '';
	width: 100%;
	height: 10px;
	background: #E1E4E9;
	position: absolute;
	left: -100%;
	top: 1em;
	z-index: -1; /*put it behind the numbers*/
}
#progressbar li:first-child:after {
	/*connector not needed before the first step*/
	content: none; 
}
/*marking active/completed steps blue*/
/*The number of the step and the connector before it = grey*/
#progressbar li.active:before,  #progressbar li.active:after{
	background: #52BAC7;
	color: white;
}
/*buttons*/
.btn-sub{
	display: inline-block;
	width: 9em;
